import React, { Component } from 'react';

// import PropTypes from 'prop-types';

export class ContactList extends Component {
  render() {
    console.log('props', this.props.props);

    return (
      <div>
        <h2> Contacts </h2>
        <ul>
          {this.props.props.map(item => (
            <li key={item.id}>
              {item.name}: {item.number}
            </li>
          ))}
        </ul>
      </div>
    );
  }
}

